body {
  margin: 0;
  font-family: "Roboto";
}

.container {
  margin: 0 auto;
  box-sizing: border-box;
  max-width: 1020px;
  height: 100%;
}

.wrapper {
  position: relative;
  z-index: 200;
  background-color: $c-main-color-white;
}

.section {
  position: relative;
}

.section__title {
  display: block;
  position: absolute;
  margin: 0;
  left: 50%;
  transform: translate(-50%, 0);
  top: 0;
  font-style: normal;
  font-weight: 700;
  font-size: $c-main-font-size * 4.8;
  line-height: $c-main-font-size * 4.8;
  letter-spacing: -1.5px;
}

.crown {
  position: absolute;
  background-image: url("../images/crown.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  height: 16px;
  width: 23px;
  left: 50%;
  top: 53px;
  transform: translate(-50%, 0);
}

.section__left {
  display: flex;
  width: 50%;
}

.one-column {
  margin-right: 111px;
}

.section__rigth {
  display: flex;
  justify-content: flex-end;
  width: 50%;
}

