.testimonials {
  padding-top: 128px;
  padding-bottom: 104px;

  &__container {
    display: flex;
    justify-content: space-between;
  }

  &__person {
    display: flex;
    align-items: center;
    flex-direction: column;
    max-width: 310px;
  }

  &__photo {
    position: relative;
    height: 150px;
    width: 150px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    border-radius: 50%;
    margin-bottom: 32px;

    &:after {
      content: "";
      position: absolute;
      display: block;
      background-repeat: no-repeat;
      height: 30px;
      width: 40px;
      top: 56px;
      left: -22px;
    }
  }

  &__name {
    margin: 0 0 4px;
    font-style: normal;
    font-weight: 700;
    font-size: $c-main-font-size * 2.4;
    line-height: $c-main-font-size * 3;
    color: $c-main-color-blue;
  }

  &__position {
    margin-bottom: 4px;
    font-style: normal;
    font-weight: 400;
    font-size: $c-main-font-size * 1.4;
    line-height: $c-main-font-size * 2.7;
    color: #9FADC2;
  }

  &__text p {
    padding: 0 5px;
    text-align: center;
    font-style: normal;
    font-weight: 400;
    font-size: $c-main-font-size * 1.6;
    line-height: $c-main-font-size * 2.7;
    color: $c-main-color-gray;
  }
}
