.best {
  position: relative;
  padding: 104px 18px;

  .container {
    display: flex;
    justify-content: space-between;
  }

  &__Real-estate {
    width: 50%;

    font-style: normal;
    font-weight: 700;
    font-size: $c-main-font-size * 4.8;
    line-height: $c-main-font-size * 4.8;
    align-items: center;
    letter-spacing: -1.5px;


    & span {
      display: block;
    }

    & a {
      text-decoration: none;
      color: $c-main-color-blue;
    }
  }

  &__text {
    width: 50%;

    & span {
      font-style: normal;
      font-weight: 700;
      font-size: $c-main-font-size * 2.4;
      line-height: $c-main-font-size * 3;
      color: $c-main-color-blue;
    }

    & > p {
      font-style: normal;
      font-weight: 400;
      font-size: $c-main-font-size * 1.6;
      line-height: $c-main-font-size * 2.7;
      color: $c-main-color-gray;
    }

    & strong {
      font-style: normal;
      font-weight: 700;
      font-size: $c-main-font-size * 1.6;
      line-height: $c-main-font-size * 2.7;
      color: $c-main-color-gray;
    }
  }
}
