.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 359px;
  width: 100%;

  background-image: url("../images/footer.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  &__logo {
    position: absolute;
    bottom: 35px;
    display: flex;
    justify-content: center;
    height: $c-main-logo-height;
    width: $c-main-logo-width;

    & img {
      height: 100%;
    }
  }

  &__copy {
    position: absolute;
    bottom: 0px;

    font-style: normal;
    font-weight: 500;
    font-size: $c-main-font-size * 1.6;
    line-height: $c-main-font-size * 3.6;
    color: $c-main-color-white;

    text-transform: uppercase;
    text-align: center;
  }
}
