@media screen and (max-width: 600px) {
  .first {
    .container {
      min-height: 568px;
    }
  }

  .container-menu {
    min-height: 568px;
  }

  .header {
    &__cross img {
      height: 16px;
      width: 16px;
    }

    &__right-block {
      height: 17px;
      width: 17px;
    }

    &__logo {
      height: 46px;
      width: 79px;
    }

    &__phone {
      width: 135px;
      height: 18px;

      & span {
        right: 5px;
        padding-top: 25px;
        padding-right: 0;
        font-weight: 500;
      }

      & img {
        height: 18px;
        width: 18px;
      }

      &:hover span {
        font-size: $c-main-font-size;
      }
    }
  }

  .container-name {
    &__name {
      margin: 0;
      color: $c-main-color-white;
      font-size: $c-main-font-size * 4.6;
      line-height: $c-main-font-size * 4;
      letter-spacing: -1px;
    }
  }

  .best {
    padding: 64px 18px;

    .container {
      display: block;
    }

    &__Real-estate {
      width: 100%;
      padding-bottom: 48px;

      font-size: $c-main-font-size * 3.6;
      line-height: $c-main-font-size * 3.6;
    }

    &__text {
      width: 100%;
    }
  }

  .galery {
    padding-top: 81px;
    padding-bottom: 64px;
  }

  .section__title {
    font-size: $c-main-font-size * 3.6;
    line-height: $c-main-font-size * 3.6;
  }

  .crown {
    top: 43px;
  }

  .apart {
    &__container {
      .apart__photo {
        height: 131px;
      }
    }

    &__title {
      padding: 16px 0 32px;
    }
  }

  .button {
    width: 100%;
  }

  .footer {
    height: 206px;

    &__logo {
      height: 46px;
      width: 79px;
      bottom: 30px;
    }

    &__copy {
      font-size: $c-main-font-size * 1.2;
      line-height: $c-main-font-size * 2.7;
    }
  }
}

@media screen and (max-width: 820px) {
  .section__left {
    display: block;
    width: 100%;
  }

  .section__rigth {
    display: flex;
    justify-content: flex-end;
    width: 100%;
  }

  .one-column {
    margin-right: 0;
  }

  .contact {
    padding-top: 81px;
    padding-bottom: 64px;

    .container {
      display: block;
    }
  }

  .button-form {
    max-width: 411px;
  }

  .section__field {
    max-width: 411px;

    .textarea {
      width: 100%;
    }

    .input {
      width: 100%;
    }
  }
}

@media screen and (max-width: 900px) {
  .apart {
    &__container {
      .apart__photo {
        width: 47%;
      }
    }
  }

  .testimonials {
    padding-top: 81px;
    padding-bottom: 64px;

    &__container {
      display: block;
    }

    &__person {
      margin: 0 auto;
    }

    &__photo {
      margin-bottom: 28px;
    }
  }

  .one-column {
    margin-right: 35px;
  }
}
