.galery {
  position: relative;
  padding: 128px 18px 104px;
}

.apart {
  &__container {
    display: flex;
    justify-content: space-between;
    padding-top: 8px;

    .apart__photo {
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      height: 260px;
      border-radius: 8px;
    }
  }

  &__title {
    padding: 24px 0 32px;

    & a {
      text-decoration: none;
      font-weight: 700;
      font-size: $c-main-font-size * 1.6;
      line-height: $c-main-font-size * 2.7;
      color: $c-main-color-blue;
    }

    & span {
      font-weight: 400;
      font-size: $c-main-font-size * 1.6;
      line-height: $c-main-font-size * 2.7;
    }
  }
}

.little-photo {
  width: 32%;
}

.big-photo {
  width: 66%;
}

.medium-photo {
  width: 49%;
}

.button {
  display: block;
  height: 56px;
  width: 150px;
  margin: 0 auto;

  color: $c-main-color-blue;
  background-color: $c-main-color-white;
  border: 2px solid $c-main-color-blue;
  border-radius: 8px;
  box-sizing: border-box;

  font-weight: 700;
  font-size: $c-main-font-size * 1.6;
  line-height: $c-main-font-size * 1.4;
  transition: background-color 0.3s ease-in-out;

  &:hover {
    background-color: $c-main-color-dark-blue;
    color: $c-main-color-white;
    cursor: pointer;
    box-shadow: 0px 13px 27px rgba(0, 83, 181, 0.46);
  }

  &:active {
    background-color: $c-main-color-dark-blue;
    color: $c-main-color-white;
    box-shadow: 0px 13px 27px rgba(0, 83, 181, 0.46);
  }
}
