.contact {
  padding-top: 140px;
  padding-bottom: 104px;

  .container {
    display: flex;
    justify-content: space-between;
    padding-left: 20px;
    padding-right: 20px;

    span {
      font-style: normal;
      font-weight: 400;
      font-size: $c-main-font-size * 1.4;
      line-height: $c-main-font-size * 2.7;
      color: #9FADC2;
    }

    p {
      font-style: normal;
      font-weight: 400;
      font-size: $c-main-font-size * 1.6;
      line-height: $c-main-font-size * 2.7;
      color: $c-main-color-gray;
      margin: 8px 0 24px;
    }
  }
}

.button-form {
  width: 411px;
  background: $c-main-color-blue;
  box-shadow: 0px 6px 23px rgba(0, 117, 255, 0.46);
  border-radius: 8px;
  color: $c-main-color-white; 
}

.section__field {
  padding-bottom: 16px;

  .form-style {
    border: none;
    font-family: "Roboto";
    font-weight: 400;
    font-style: normal;
    font-size: $c-main-font-size * 1.4;
    line-height: $c-main-font-size * 2;
    padding-left: 17px;
    box-sizing: border-box;
    border-radius: 8px;
    background-color: #F7F8FF;
    color: $c-main-color-black;
  }

  .textarea {
    height: 144px;
    width: 411px;
    padding-top: 14px;

    &:hover {
      border: 2px solid #CCD0E0;
    }

    &:focus {
      outline-style: none;
      border: 2px solid $c-main-color-blue;
    }

    &::placeholder {
      font-family: "Roboto";
      font-size: $c-main-font-size * 1.4;
      font-style: normal;
      color: #8F99A8;
      padding: 0;
      line-height: $c-main-font-size * 2;
    }
  }

  .input {
    height: 48px;
    width: 411px;

    &:hover {
      border: 2px solid #CCD0E0;
    }

    &:focus {
      outline-style: none;
      border: 2px solid $c-main-color-blue;
    }

    &::placeholder {
      font-family: "Roboto";
      font-weight: 400;
      font-style: normal;
      font-size: $c-main-font-size * 1.4;
      line-height: $c-main-font-size * 1.7;
      color: #8F99A8;
    }
  }
}
