.first {
  background-image: url("../images/header.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  .container {
    display: block;
    position: relative;
    padding-top: 0;

    min-height: 700px;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  font-style: normal;
  font-weight: 500;
  font-size: $c-main-font-size * 1.6;
  line-height: $c-main-font-size * 3.6;

  padding: 25px 24px 0;
  color: $c-main-color-white;

  &__menu {
    width: 135px;

    & a {

      &::after {
        content: url("../images/menu.svg");
      }

      &:hover::after {
        content: url("../images/menu_hover.svg");
      }
    }
  }

  &__cross img {
    height: 21px;
    width: 21px;
  }

  &__right-block {
    height: 22px;
    width: 22px;
  }

  &__logo {
    display: flex;
    justify-content: center;
    height: $c-main-logo-height;
    width: $c-main-logo-width;

    & img {
      height: 100%;
    }
  }

  &__phone {
    position: relative;
    width: 135px;
    height: 24px;

    & span {
      position: absolute;
      right: 20px;
      font-size: 0;
      font-weight: 700;
      padding-right: 20px;
      transition: font-size 0.2s ease-in-out;
    }

    & img {
      height: 24px;
      width: 24px;
    }

    & a {
      position: absolute;
      right: 0;
      display: inline-flex;
      justify-content: center;
      align-content: center;
    }

    &:hover span {
      font-size: $c-main-font-size * 1.2;
    }

    &:active {
        color: darken(grey, 20%);
      }
  }
}

.container-name {
  position: absolute;
  bottom: 80px;
  padding-left: 20px;

  &__top-line {
    margin: 0 0 24px 4px;
    height: 2px;
    width: 40px;
    background-color: $c-main-color-white;
    border-radius: 2px;
  }

  &__name {
    margin: 0;
    color: $c-main-color-white;
    font-size: $c-main-font-size * 8;
    line-height: $c-main-font-size * 8;
    letter-spacing: -1.5px;
    font-weight: 700;
  }
}

.container-menu {
  visibility: hidden;
  position: absolute;
  min-height: 700px;
  width: 100%;
  background-color: $c-main-color-blue;
  top: 0;
  left: 0;

  &__center {
    max-width: 1068px;
    margin: 0 auto;
  }
}

.navigation {
  display: block;
  padding: 50px 0 0 24px;
  box-sizing: padding-box;

  &-list {
    display: block;
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;

    &__item {
      padding-bottom: 24px;
    }

    &__link {
      font-style: normal;
      font-weight: 700;
      font-size: $c-main-font-size * 2.2;
      line-height: $c-main-font-size * 2.2;
      letter-spacing: 2px;
      text-decoration: none;
      text-transform: uppercase;
      color: $c-main-color-white;
      position: relative;
      padding-bottom: 8px;


      &:hover::after {
        display: block;
        content: "";
        width: 100%;
        height: 1.5px;
        background-color: rgb(97, 192, 255);
        border-radius: 8px;
        position: absolute;
        bottom: 0;
      }
    }
  }

  &-connection {
    display: block;
    list-style: none;
    margin: 0;
    padding: 16px 0 0 0;
    width: 100%;

    &__item {
      padding-bottom: 8px;
    }

    &__link {
      font-style: normal;
      font-weight: 700;
      font-size: $c-main-font-size * 1.2;
      line-height: $c-main-font-size * 1.6;
      letter-spacing: 2px;
      text-decoration: none;
      text-transform: uppercase;
      color: $c-main-color-white;
      position: relative;
      padding-bottom: 8px;


      &:hover::after {
        display: block;
        content: "";
        width: 100%;
        height: 1.5px;
        background-color: rgb(97, 192, 255);
        border-radius: 8px;
        position: absolute;
        bottom: 0;
      }
    }
  }
}
